.sidebar{
    background: linear-gradient(to right bottom, #664BEB, #342CCB);
    display: flex;
    flex-direction: column;
    width: 15vw;
    height: 100vh;
    align-items: center;
    gap: 4vw;
}

.sidebar button{
    background: none;
    border: none;
    color: white;
    font-size: 1.5rem;
    cursor: pointer;
    transition: 0.3s;
    font-size: 1.5vw;
}

.sidebar p{
    color: white;
    font-size: 2rem;
    margin-top: 5vh;

}

.sidebar button:hover{
    transform: scale(1.1);
}

.sidebar button.active{
    color: #664BEB;
    background: white;
    border-radius: 10px;
}