.calendar-container {
    display: flex;
    background-color: #C6DCBA;
}

.calendar {
    margin: 20px auto;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 30vh;
    width: 650px;
    height: 320px;
}

.calendar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: green;
}

.calendar-days {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 1px;
}

.calendar-day {
    padding: 10px;
    text-align: center;
    border: 1px solid black;
}

.calendar-day:hover {
    background-color: yellow;
}

button {
    cursor: pointer;
}