.dash{
    display: flex;
    flex-direction: row;
    position: relative;
    background-color: #C6DCBA;
}


.dashmain{
    display: flex;
    flex-direction: column;
}

.rightdashboard{
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.chart{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 5vw;
    margin-top: 5vh;
}


.div{
    height: 100vh;
    width: 100vw;  
    background-color: #7F27FF;
}

.div img{
    height: 98vh;
    width: 85vw;
    margin-left: 1vw;
    margin-top: 1vh;
}
    
