/* TodoList.css */

.todo-list {
    width: 30vw;
    height: 25vw;
    margin: 0 auto;
    padding: 20px;
    background-color: #EBF400;
    border: 2px solid #ccc;
    border-radius: 20px;
}

.todo-list h2 {
    margin-bottom: 10px;
    padding-left: 2vw;
    color: #333;
}

.todo-list ul {
    list-style-type: none;
    padding: 0;
}

.todo-list li {
    margin-bottom: 10px;
    height: 1.5vw;
    padding: 1vw;
    border-radius: 20px;
    background: linear-gradient(to right bottom, #FDBB00, pink);
}

.todo-list li input[type="checkbox"] {
    margin-right: 10px;
}

.todo-list li.completed {
    background: linear-gradient(to right bottom, #664BEB, #342CCB);
    /* Change background color for completed tasks */
}

.todo-list li.completed span {
    text-decoration: line-through;
    color: #999;
}