/* TopBar.css */

.topbar-container {
    background: linear-gradient(to right bottom, #59B4C3, #342CCB);
    color: #fff;
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 85vw;
    height: 4vw;
    border-radius: 20PX;
    margin: 0.5VW;
}

.topbar-title {
    font-size: 1.2rem;
}

.topbar-signout-btn {
    background-color: #fff;
    color: #333;
    border: none;
    padding: 1vw 2vw;
    border-radius: 5px;
    cursor: pointer;
}

.topbar-signout-btn:hover {
    background-color: #ddd;
}